.screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  transition: margin 0.5s ease-out;
  background: linear-gradient(90deg, #29323C 0%, #485563 100%);
}
.title {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  max-width: 600px;
  margin: 0 auto;
}
h1 {
  font-size: 32pt;
  line-height: 36pt;
  margin-bottom: 20px;
  & strong {
    color: #fd7e14;
  }
}
.button {
  border: 2px solid #fff;
  box-sizing: inherit;
  cursor: pointer;
  display: inline-block;
  font-size: 13pt;
  padding: 10px 20px;
  transition: all 0.5s;
}
.view {
  display: block;
  font-size: 1.5rem;
  text-decoration: none;
  position: relative;
  color: #fff;
  margin-bottom: 50px;
  &:hover {
    background-color: #16D9E3;
    border: 2px solid #16D9E3;
    color: #fff;
    & i {
      transform: rotate(90deg);
    }
  }
  & i {
    position: absolute;
    right: 10px;
    top: 21px;
    transition: transform 350ms ease;
    &:hover {
      color: #fff;
    }
  }
}

.screen.up {
  margin-top: -100vh;
}

.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 15px 40px 15px 40px;
  display: inline-block;
  border-radius: 100px;
  transition: all .2s;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn-white {
  background-color: #fff;
  color: #777;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn-white::after {
  background-color: #fff;
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn-animated {
  animation: moveInBottom 5s ease-out;
  animation-fill-mode: backwards;
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.bublcontent {
  position: relative;
  z-index:100;
}
.bubl {
